html,
body {
  margin: 0;
  padding: 0;
  font-size: 0.213333rem;
}

body::after {
  display: block;
  content: "";
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: url(data:image/png;base64,) repeat-x;
  background-size: 100% 100%;
}

.modal {
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  position: fixed;
  z-index: 10;
  background: rgba(0, 0, 0, 0.72);
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
}

.spop {
  background: #fff;
  padding: 0.266667rem;
}

#recordCanvas {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 20;
}

.bridgeGame .loading {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 16;
  background: url(img/title.69f8b0e.png) no-repeat center;
  background-size: 7.8rem 1.76rem;
  background-color: #ffffff;
}

.bridgeGame .one-line {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.bridgeGame .game {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 3;
}

.bridgeGame .game .controll {
  position: absolute;
  right: 0.266667rem;
  top: 0.4rem;
  width: 0.8rem;
  height: 0.8rem;
  line-height: 0.8rem;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: contain;
}

.bridgeGame .game .controll.auto {
  background-image: url(data:image/png;base64,);
}

.bridgeGame #renderCanvas {
  width: 100%;
  height: 100%;
  touch-action: none;
}

.bridgeGame .red {
  color: red;
}

.bridgeGame .pages {
  width: 10rem;
  height: 16.08rem;
  position: relative;
}

.bridgeGame .letter {
  font-size: 0.4rem;
}

.bridgeGame .pop-mask {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.72);
  display: none;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
}

#bridgeGame .insp-wrapper {
  font-size: 0.213333rem;
}

