  height: 2rem;
  background: url(img/baoza2.5b8834f.png);
  background-size: 2rem 12rem;
  position: absolute;
  left: 4rem;
  top: 0.266667rem;
  -webkit-animation: baoza2 600ms steps(6);
  animation: baoza2 600ms steps(6);
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.RecordSkin .star {
  background: url(img/xingxing.b77ef6c.png) no-repeat center;
  background-size: 1.066667rem 31.893333rem;
  -webkit-animation: star 1800ms steps(26) infinite;
  animation: star 1800ms steps(26) infinite;
}

@-webkit-keyframes baoza1 {
  100% {
    background-position-y: -71.466667rem;
    opacity: 0;
  }
}

@keyframes baoza1 {
  100% {
    background-position-y: -71.466667rem;
    opacity: 0;
  }
}

@-webkit-keyframes baoza2 {
  100% {
    background-position-y: -12rem;
    opacity: 0;
  }
}

@keyframes baoza2 {
  100% {
    background-position-y: -12rem;
    opacity: 0;
  }
}

@-webkit-keyframes star {
  100% {
    background-position-y: -31.893333rem;
  }
}

@keyframes star {
  100% {
    background-position-y: -31.893333rem;
  }
}

.debug {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 0.4rem;
  z-index: 20;
  font-size: 0.16rem;
  background: rgba(0, 0, 0, 0.3);
  color: #ffffff;
  -webkit-transition: all 200ms;
  transition: all 200ms;
  overflow-y: auto;
  display: none;
}

.debug .tip {
  padding: 0.12rem;
  border-bottom: solid 0.013333rem #e4e4e4;
}

.debug.show {
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
}

